<template>
  <view class="page-container">
    <!-- Ëá™ÂÆö‰πâÂØºËà™Ê†è -->
    <view class="custom-navbar">
      <!-- Áä∂ÊÄÅÊ†èÂç†‰Ωç -->
      <view class="status-bar" :style="{ height: statusBarHeight + 'px' }"></view>
      <!-- ÂØºËà™Ê†èÂÜÖÂÆπ -->
      <view class="navbar-content">
        <view class="navbar-left" @click="onBack">
          <text class="back-icon">‚Üê</text>
        </view>
        <view class="navbar-center">
          <text class="navbar-title">ÂèÇÂä†ÁöÑÊ¥ªÂä®</text>
        </view>
        <view class="navbar-right">
          <!-- Âè≥‰æßÂèØ‰ª•ÊîæÁΩÆÂÖ∂‰ªñÊìç‰ΩúÊåâÈíÆ -->
        </view>
      </view>
    </view>
    
    <!-- TabÊ†è -->
    <view class="tab-bar">
      <view class="tab-bar-placeholder" :style="{ height: (statusBarHeight + 44) + 'px' }"></view>
      <view class="tab-container">
        <view 
          class="tab-item" 
          :class="{ active: activeTab === 'active' }"
          @click="switchTab('active')"
        >
          <text class="tab-text">Êú™ËøáÊúü</text>
          <view class="tab-indicator" v-if="activeTab === 'active'"></view>
        </view>
        <view 
          class="tab-item" 
          :class="{ active: activeTab === 'expired' }"
          @click="switchTab('expired')"
        >
          <text class="tab-text">Â∑≤ËøáÊúü</text>
          <view class="tab-indicator" v-if="activeTab === 'expired'"></view>
        </view>
      </view>
    </view>
    
    <!-- ÂÜÖÂÆπÂå∫Âüü -->
    <view class="content-container">
      <!-- Âç†‰ΩçÂå∫ÂüüÔºåÈÅøÂÖçË¢´ÂØºËà™Ê†èÂíåtabÊ†èÈÅÆÊå° -->
      <view class="content-placeholder" :style="{ height: (statusBarHeight + 44 + 50) + 'px' }"></view>
      
      <!-- Ê¥ªÂä®ÂàóË°® - Áªü‰∏Ä‰ΩøÁî®scroll-view -->
      <scroll-view 
        class="activity-list" 
        scroll-y="true" 
        :show-scrollbar="false"
        :enable-back-to-top="false"
      >
        <view class="list-content">
          <view v-if="currentActivityList.length === 0" class="empty-state">
            <view class="empty-icon">üìÖ</view>
            <text class="empty-text">{{ activeTab === 'active' ? 'ÊöÇÊó†Êú™ËøáÊúüÁöÑÊ¥ªÂä®' : 'ÊöÇÊó†Â∑≤ËøáÊúüÁöÑÊ¥ªÂä®' }}</text>
          </view>
          
          <view v-else class="activity-items">
            <ActivityTicketList
              v-for="activity in currentActivityList"
              :key="activity.id"
			  :status="activeTab" 
              :activityData="activity"
              @action="handleAction"
              @cancel="handleCancel"
            />
          </view>
        </view>
      </scroll-view>
    </view>
  </view>
</template>

<script setup>
import { ref, onMounted, nextTick, computed } from 'vue'
import ActivityTicketList from '@/components/ActivityTicket/ActivityTicketList.vue'// Ê†πÊçÆ‰Ω†ÁöÑÂÆûÈôÖË∑ØÂæÑË∞ÉÊï¥
import { useInfoStore } from '@/store/Info.js'
import {cancelapply} from '@/new-apis/events.js'

const useinfo = useInfoStore()

// Ëé∑ÂèñÁ≥ªÁªüÁä∂ÊÄÅÊ†èÈ´òÂ∫¶
const statusBarHeight = ref(0)

// TabÁõ∏ÂÖ≥Áä∂ÊÄÅ
const activeTab = ref('active') // 'active' | 'expired'

// Ê¥ªÂä®ÂàóË°®Êï∞ÊçÆ
const activityList = ref([])
const expiredActivityList = ref([])

// Ê†πÊçÆÂΩìÂâçtabËøîÂõûÂØπÂ∫îÁöÑÊ¥ªÂä®ÂàóË°®
const currentActivityList = computed(() => {
  return activeTab.value === 'active' ? activityList.value : expiredActivityList.value
})

// ÂàáÊç¢Tab
const switchTab = (tab) => {
  if (activeTab.value !== tab) {
    activeTab.value = tab
  }
}

// È°µÈù¢Âä†ËΩΩÊó∂Ëé∑ÂèñÊï∞ÊçÆ
onMounted(() => {
  // Ëé∑ÂèñÁ≥ªÁªü‰ø°ÊÅØ
  const systemInfo = uni.getSystemInfoSync()
  statusBarHeight.value = systemInfo.statusBarHeight || 20
   // Âä†ËΩΩÊ¥ªÂä®Êï∞ÊçÆ
  loadActivityData()
})

// Âä†ËΩΩÊ¥ªÂä®Êï∞ÊçÆ
const loadActivityData = async () => {
  try {
     await useinfo.userapply()
    activityList.value = useinfo.applyactivity
    expiredActivityList.value = useinfo.applyactivityhistory
  } catch (error) {
    console.error('Âä†ËΩΩÊ¥ªÂä®Êï∞ÊçÆÂ§±Ë¥•:', error)
    uni.showToast({
      title: 'Âä†ËΩΩÂ§±Ë¥•',
      icon: 'error'
    })
  }
}

// Â§ÑÁêÜ‰∏ªË¶ÅÊìç‰ΩúÔºàÂ¶ÇÁ≠æÂà∞Ôºâ
const handleAction = (activityData) => {
  console.log('Êìç‰ΩúÊ¥ªÂä®:', activityData)
  
  // ÂØπÂ∑≤ËøáÊúüÁöÑÊ¥ªÂä®‰∏çÂÖÅËÆ∏Êìç‰Ωú
  if (activeTab.value === 'expired') {
    uni.showToast({
      title: 'Ê¥ªÂä®Â∑≤ËøáÊúüÔºåÊó†Ê≥ïÊìç‰Ωú',
      icon: 'none'
    })
    return
  }
  
  uni.showToast({
    title: `ÊâßË°å: ${activityData.statusText}`,
    icon: 'success'
  })
  
  // ËøôÈáåÊ∑ªÂä†‰Ω†ÁöÑÂÖ∑‰Ωì‰∏öÂä°ÈÄªËæë
  // ÊØîÂ¶ÇË∑≥ËΩ¨Âà∞Á≠æÂà∞È°µÈù¢„ÄÅÊâßË°åÁ≠æÂà∞Êìç‰ΩúÁ≠â
}

// Â§ÑÁêÜÂèñÊ∂àÊä•Âêç
const handleCancel = (activityData) => {
  console.log('ÂèñÊ∂àÊä•Âêç:', activityData)
  
  // ÂØπÂ∑≤ËøáÊúüÁöÑÊ¥ªÂä®‰∏çÂÖÅËÆ∏ÂèñÊ∂àÊä•Âêç
  if (activeTab.value === 'expired') {
    uni.showToast({
      title: 'Ê¥ªÂä®Â∑≤ËøáÊúüÔºåÊó†Ê≥ïÂèñÊ∂àÊä•Âêç',
      icon: 'none'
    })
    return
  }
  
  uni.showModal({
    title: 'Á°ÆËÆ§ÂèñÊ∂à',
    content: `Á°ÆÂÆöË¶ÅÂèñÊ∂àÊä•Âêç"${activityData.title}"ÂêóÔºü`,
    success: (res) => {
      if (res.confirm) {
        // ÊâßË°åÂèñÊ∂àÊä•ÂêçÁöÑÈÄªËæë
        cancelSignUp(activityData)
      }
    }
  })
}

// ÂèñÊ∂àÊä•ÂêçÁöÑÂÖ∑‰ΩìÂÆûÁé∞
const cancelSignUp = async (activityData) => {
  try {
    await cancelapply(activityData.id)
    loadActivityData()
    
    uni.showToast({
      title: 'ÂèñÊ∂àÊä•ÂêçÊàêÂäü',
      icon: 'success'
    })
  } catch (error) {
    console.error('ÂèñÊ∂àÊä•ÂêçÂ§±Ë¥•:', error)
    uni.showToast({
      title: 'ÂèñÊ∂àÂ§±Ë¥•',
      icon: 'error'
    })
  }
}

// ËøîÂõûÂáΩÊï∞
function onBack() {
  uni.navigateBack()
}
</script>

